/* latin */
$path-to-assets: "assets" !default;
@font-face{
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  src: local('Montserrat Regular'), 
       local('Montserrat-Regular'), 
       url(#{$path-to-assets}/fonts/Montserrat/Montserrat-Regular.woff2) format('woff2'),
       url(#{$path-to-assets}/fonts/Montserrat/Montserrat-Regular.woff) format('woff'),
       url(#{$path-to-assets}/fonts/Montserrat/Montserrat-Regular.ttf) format('truetype');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2212, U+2215;
}
