@mixin set-font-primary{
  font-family: 'Montserrat', 'San Francisco', Tahoma, sans-serif;
}
@mixin set-font-secondary{
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
}
@mixin set-font-tertiary{
  font-family: 'Roboto Slab', 'Apple Garamond', 'Utopia', serif;
}
@mixin set-font-code{
  font-family: 'Inconsolata', 'Consolas', 'Monaco', 'Courier New', monospace;
}
@mixin set-font{
  @include set-font-primary;
}

html.responsive{
  font-size: 16pt;
  @media (max-width: 1000px){font-size: 8pt;}
  @media (min-width: 1001px) and (max-width: 1366px){font-size: 10pt;}
  @media (min-width: 1367px) and (max-width: 1600px){font-size: 11pt;}
  @media (min-width: 1601px) and (max-width: 1920px){font-size: 12pt;}
  @media (min-width: 1921px) {font-size: 16pt;}
}