$primary-darkest:   #0b223d;
$primary-dark:      #063770;
$primary-main:      #1a83de;
$primary-light:     #0091da;
$primary-lightest:  #a4dceb;

$secondary-dark:    #ee531f;
$secondary-main:    #ff8d3d;
$secondary-light:   #ffc000;

$orth-darkest:  #18191b;
$orth-darker:   #20242b;
$orth-dark:     #292c31;
$orth-main:     #6b7b90;
$orth-light:    #b9c0cc;
$orth-lighter:  #d1d6dd;
$orth-lightest: #f0f2f4;

$black:        #000000;
$grey-darkest: #020202;
$grey-darker:  #212121;
$grey-dark:    #424242;
$grey-main:    #757575;
$grey-light:   #e0e0e0;
$grey-lightest:#f5f5f5;
$white:        #ffffff;

$graph-color-1: #f9bc38;
$graph-color-2: #85c72e;
$graph-color-3: #27c3ee;
$graph-color-4: #df3931;
$graph-color-5: #8f50b6;
$graph-color-6: #db1c9b;
$graph-color-7: #e63a13;



$primary: $primary-main;
$secondary: $secondary-main;
$accent-color: $primary;

$header-bg: darken($orth-dark, 6%);
$header-fg: $grey-lightest;
$content-bg: $grey-lightest;
$content-fg: $orth-dark;
$breadcrumb-bg: $orth-lighter;
$breadcrumb-fg: $orth-dark;
$breadcrumb-highlight-fg: darken($primary, 10%);
