$message-bar-critical-bg: $secondary-dark;
$message-bar-fg: transparentize(white, 0.3);
$nav-bg: $orth-lighter;
$nav-fg: transparentize(black, 0.16) ;
$main-fg: $grey-darkest;
$footer-bg: $orth-light;
$footer-fg: $orth-dark;
$progress-border-color: $orth-main;
$progress-animation-fg: $primary-main;
$progress-bar-fg: darken($orth-dark, 6%);
