$button-bg: $primary;
$button-highlight-bg: lighten($primary, 3%);
$button-highlight-border-color: $orth-dark;
$button-disabled-bg: $orth-main;
$compact-input-fg: $grey-lightest;
$compact-input-line-color: transparentize($primary-light, 0.5);
$compact-textarea-inner-line-color: transparentize($primary-light, 0.9);
$compact-input-placeholder-color: $orth-main;
$light-compact-input-fg: $grey-dark;
