$nav-fg: $grey-dark;
$nav-hover-fg: $grey-dark;
$nav-hover-bg: $primary-lightest;
$nav-highlight-fg: $accent-color;
$nav-selected-bg: $primary;
$nav-selected-fg: $orth-lightest;
$nav-selected-group-bg: $primary-dark;
$nav-outline-color: $secondary-dark;
