#page-container{
  display: grid;
  grid-template-columns: var(--nav-width) 1fr;
  grid-template-rows: 5rem 1.8rem 1fr 1.8rem;
  grid-gap: 0 0;
  width: 100%; height: 100%;
  overflow: hidden;
  &.nav-pinned{
    &>#breadcrumb-bar, &>main{
      grid-column: 2/span 1;
    }
    #main-nav{
      transform: translateX(0);  
    }
  }
}
#header{grid-row: 1/span 1;}
#breadcrumb-bar{grid-row: 2/span 1;}
main{
  grid-row: 3/span 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  --rhs-width: 100vw;
  width: var(--rhs-width);
  height: calc(100vh - 8.6rem);

  #main-nav.pinned ~ &{
    --rhs-width: calc(100vw - var(--nav-width));
    width: var(--rhs-width);
  }
}
#main-content{
  width: 100%; height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  &>.content{
    grid-column: 1/-1; grid-row: 1/-1;
    display: none;
  }
}
#main-content:before{
  content: '';
  background-repeat: no-repeat;
  opacity: 0.05;
  grid-column: 1/-1; grid-row: 1/-1;
  transition: background-image 0.3s ease-in-out;
  @supports not (clip-path: circle(40%)){
    display: none;
  }
}
#footer{grid-row: 4/span 1;}
#header, #breadcrumb-bar, main, #footer{
  grid-column: 1/-1;
}
#main-nav{
  transform: translateX(-105%);
  transition: transform 200ms ease-in;
  grid-column: 1/span 1;
  grid-row: 1/-2;
  display: flex;
  flex-direction: column;
  justify-content: stretch;
  align-items: stretch;
  z-index: 2;
  &.on{
    transform: translateX(0);
  }
}
#mask{
  grid-row: 1 / span 3;
  grid-column: 2 / -1;
  z-index: 1;
  &.dialog, &.progress{
    grid-row: 1/span 3;
    grid-column: 1/-1;
  }
}
.spacer{flex-grow: 1;}