@import "css3-mixins";

@mixin fill-space {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
}

$a-tags: 'a, a:active, a:hover, a:visited';
$a-tags-hover: 'a:active, a:hover, a:focus';
$debug-force-grid-unspported: false;
$debug-force-grid-supported: false; 

@mixin link-color($color){
  #{$a-tags}, #{$a-tags-hover} {
    color: $color;
  }
}
@mixin link{
  #{$a-tags}, #{$a-tags-hover} {
    @content;
  }
}

@mixin grid-supported($checkvalue:true){
  @if ($debug-force-grid-unspported){
    @if ($checkvalue){
      // @supports not (display: grid){
      //   @content;
      // }
      .no-cssgrid &{
        @content;
      } 
    } @else {
      // @supports (display: grid){
      //   @content;
      // }
      .cssgrid &{
        @content;
      }
    }
  }
  @else if ($debug-force-grid-supported){
    @if ($checkvalue){
      // @supports (display: grid){
      //   @content;
      // }
      .cssgrid &{
        @content;
      }
    } @else {
      // @supports not (display: grid){
      //   @content;
      // }
      .no-cssgrid &{
        @content;
      }
    }
  }
  @else{
    @if ($checkvalue){
      // @supports (display: grid){
      //   @content;
      // }
      .cssgrid &{
        @content;
      }
    } @else {
      // @supports not (display: grid){
      //   @content;
      // }
      .no-cssgrid &{
        @content;
      }
    }
  }
}
@mixin grid-unsupported{
  @include grid-supported(false){
    @content;
  };
}

@mixin easytransition($name, $time: 150ms, $ease: ease-in){transition: $name $time $ease;}

@mixin full-width(){
  @include media("<=phone", "handheld", 'retina2x'){
    width: 100%;
    max-width: 100%;
  }
  @include media(">phone", "<=tablet", "handheld", "retina2x"){
    width: 100%;
    max-width: 100%;
  }
  @include media(">tablet", "<=laptop", "handheld", "retina2x"){
    width: 100%;
    max-width: 100%;
  }
  @include media(">=laptop", "<desktop"){
    width: 100%;
    max-width: map-get($breakpoints, "laptop");
  }
  @include media(">=desktop"){
    width: 100%;
    max-width: 1200px;
  }
  width: 100%;
  max-width: map-get($breakpoints, "laptop");
}

@function getbr($name){
  @return (map-get($breakpoints, $name));
}

@mixin media_stage_9{
  @media (max-width: getbr("phone") ){
    @content;
  }
}
@mixin media_stage_10{
  @media ( max-width: getbr("sweet") ), ( min-width: getbr("sweet") + 1px) and ( max-width: getbr("tablet") ) and ( orientation: portrait ){
    @content;
  }
}
@mixin media_stage_50{
  @media (min-width: getbr("sweet") + 1px) and (max-width: getbr("tablet")) {
    @content;
  }
}
@mixin media_stage_51{
  @media (min-width: getbr("sweet") + 1px){
    @content;
  }
}
@mixin media_stage_49{
  @media (max-width: getbr("sweet") ){
    @content;
  }
}
@mixin media_stage_100{
  @media ( min-width: getbr("sweet") + 1px ) and ( max-width: getbr("tablet") ) and ( orientation: landscape), ( min-width: getbr("tablet") + 1px ) {
    @content;
  }
}

@mixin media_small{
  @include media_stage_10{
    @content;
  }
}
@mixin media_lt_small{
  @include media_stage_9{
    @content;
  }
}
@mixin media_mid{
  @include media_stage_50{
    @content;
  }
}
@mixin media_gt_mid{
  @include media_stage_51{
    @content;
  }
}
@mixin media_lt_mid{
  @include media_stage_49{
    @content;
  }
}
@mixin media_lt_big{
  @media (min-width: getbr("sweet") + 1px) and (max-width: getbr("wide")){
    @content;
  }
}
@mixin media_big{
  @include media_stage_100{
    @content;
  }
}

//
//  Function to create an optimized svg url
//
@function svg-url($svg){
    //
    //  Chunk up string in order to avoid
    //  "stack level too deep" error
    //
    $encoded:'';
    $slice: 2000;
    $index: 0;
    $loops: ceil(str-length($svg)/$slice);
    @for $i from 1 through $loops {
        $chunk: str-slice($svg, $index, $index + $slice - 1);
        //
        //   Encode (may need a few extra replacements)
        //
        $chunk: str-replace($chunk,'"','\'');
        $chunk: str-replace($chunk,'<','%3C');
        $chunk: str-replace($chunk,'>','%3E');
        $chunk: str-replace($chunk,'&','%26');
        $chunk: str-replace($chunk,'#','%23');
        $encoded: #{$encoded}#{$chunk};
        $index: $index + $slice;
    }
    @return url("data:image/svg+xml;charset=utf8,#{$encoded}");
}

//  Helper function to replace characters in a string
@function str-replace($string, $search, $replace: '') {
    $index: str-index($string, $search);
    @if $index {
        @return str-slice($string, 1, $index - 1) + $replace +
            str-replace(str-slice($string, $index +
            str-length($search)), $search, $replace);
    }
    @return $string;
}

@mixin slideDown($h:5rem, $t:0.5s){
  overflow: initial;
  max-height: $h;
  transition: max-height $t;
}

@mixin slideUp($h:0, $t:0.05s){
  overflow: hidden;
  max-height: $h;
  transition: max-height $t;
}

@mixin display-grid {
  display: -ms-grid;
  display: grid;
}

@mixin grid-col ($col-start, $col-end) {
  -ms-grid-column: $col-start;
  -ms-grid-column-span: $col-end - $col-start;
  grid-column: #{$col-start}/#{$col-end};
}
@mixin grid-row ($row-start, $row-end) {
  -ms-grid-row: $row-start;
  -ms-grid-row-span: $row-end - $row-start;
  grid-row: #{$row-start}/#{$row-end};
}
@mixin grid-child ($col-start, $col-end, $row-start, $row-end) {
  @include grid-col($col-start, $col-end);
  @include grid-row($row-start, $row-end);
}

@mixin grid-col-span($col-start, $col-span){
  -ms-grid-column: $col-start;
  -ms-grid-column-span: $col-span;
  grid-column: #{$col-start}/span #{$col-span};
}
@mixin grid-row-span($row-start, $row-span){
  -ms-grid-row: $row-start;
  -ms-grid-row-span: $row-span;
  grid-row: #{$row-start}/span #{$row-span};
}
@mixin grid-span ($col-start, $col-span, $row-start, $row-span){
  @include grid-col-span($col-start, $col-span);
  @include grid-row-span($row-start, $row-span);
}
