#messageBar{
  background-color: $message-bar-info-bg;
  font-size: 0.9rem;
  font-family: "OpenSans";
  color: $message-bar-fg;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  padding: 0.5rem 1.1rem;
  border-top: 1px solid $header-bg;
  display: flex;
  margin-top: -2.4rem;
  transition: margin-top 0.3s ease-in-out;
  flex-basis: 1.3rem;
  flex-shrink: 0;
  &.error,&.warning,&.info,&.critical{margin-top: 0;}
  svg{
    width: 1.2rem;
    height: 1.2rem;
    path{fill: $message-bar-fg;}
  }
  .icon{
    display: none;
    flex-direction: row;
    align-items: center;
    margin-right: 1rem;
  }
  &.info {background-color: $message-bar-info-bg; .icon.info {display: flex;}}
  &.warning {background-color: $message-bar-warning-bg; .icon.warning{display: flex;}}
  &.error {background-color: $message-bar-error-bg; .icon.error{display: flex;}}
  &.critical {background-color: $message-bar-critical-bg; .icon.critical{display: flex;}}
  .content{flex-grow: 1;}
  .close{
    cursor: pointer;
    &:hover svg path{fill: white;}
  }
}