@import "nav";

#header{
  display: grid;
  grid-template-columns: minmax(19rem, 20%) 1fr;
  background-color: $header-bg;
}
#logo{
  grid-column: 1/span 2;
  grid-row: 1/span 1;
  justify-self: end;
  align-self: center;
  margin-right: 1rem;
  svg{width: 3rem;}
}
#app-title{
  grid-column: 1/span 2;
  grid-row: 1/span 1;
  justify-self: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
#app-name{
  text-align: center;
  color: $primary;
  font-size: 3em;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  line-height: 0.9em;
  margin-top: -0.1em;
  margin-left: 0.4em;
}
#tagline{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: $header-fg;
  font-size: 0.7rem;
  svg{
    width: 0.5em; height: 0.5em; margin: 0 0.5em;
    --pentagon-rotation-angle: 10deg;
    @for $i from 1 through 3 {
      &:nth-of-type(#{$i}){
        transform: rotateZ( calc( #{$i} * var(--pentagon-rotation-angle) ) );
      }
    }
    
  }
  path{fill: $header-fg;}
}

#breadcrumb-bar{
  background-color: $breadcrumb-bg;
  border-bottom: 1px solid darken($breadcrumb-bg, 15%);
  border-top: 1px solid lighten($breadcrumb-bg, 5%);
  background-image: linear-gradient(to bottom, transparentize($black, 0.76) 0%, transparentize($black, 0.91) 60%, transparentize($black, 0.92) 100%);
  ul{
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    margin-left: 1rem;
  }
  li{
    margin-top: 0.1rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    &:after{
      content: '\0276F';
      margin: 0 1rem;
      display: block;
      font-size: 0.8rem;
      transform: scaleY(0.8);
      color: $breadcrumb-separator-fg;
    }
    &:last-of-type:after{
      display: none;
    }
  }
  a{
    display: block;
    text-transform: uppercase;
    font-size: 0.8rem;
    letter-spacing: 0.05rem;
    font-weight: 600;
  }
  #{$a-tags}{
    color: $breadcrumb-fg;
    text-decoration: none;
  }
  #{$a-tags-hover}{
    color: $breadcrumb-highlight-fg;
    outline: 0;
  }
}