#footer{
  background-color: $footer-bg;
  z-index: 2;
  position: relative;
  box-shadow: inset 1px 1px 0px 0px darken($footer-bg, 10%);
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: stretch;
  align-items: stretch;
  .spacer{
    flex-basis: 0px;
    flex-grow: 1;
    flex-shrink: 1;
  }

  #progress{ 
    flex-basis: 20rem;
    flex-grow: 0;
    flex-shrink: 0;
    border-left: 1px solid $progress-border-color;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: stretch;
    align-items: stretch;
    &:before{content: ''; display: block;width: 1px; background-color: lighten($footer-bg, 10%); flex-grow: 0; flex-shrink: 0;}
    &.on{
      border-radius: 3px;
      background-image: 
        repeating-linear-gradient(
          -45deg,
          transparentize($progress-animation-fg, 0.3),
          transparentize($progress-animation-fg, 0.7) 11px,
          $footer-bg 10px,
          $footer-bg 20px /* determines size */
        );
      background-size: 28px 28px;
      animation: progress-move .5s linear infinite;
    }
  }
  #progress-message{
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    margin-left: 1rem;
    color: $progress-bar-fg;
    font-weight: bold;
    flex-grow: 1;
  }
}

@keyframes progress-move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 28px 0;
  }
}
