$transition-duration: 100ms;
:root{
  --nav-width: 20rem;
}
#main-nav{
  background-color: $nav-bg;
  position: relative;
  box-shadow: -1rem 0 1rem 1rem black;
  .content{
    flex-grow: 1;
    flex-shrink: 0;
    height: calc(100vh - 6.8rem);
    overflow-y: auto;
    background-image: linear-gradient(76deg, transparentize(black, 1) 0%, transparentize(black, 1) 50%, transparentize(black, .98) 50%, transparentize(black, 1) 100%);
  }
  &.pinned{
    box-shadow: none;
    .content{
       border-right: 1px solid darken($nav-bg, 5%); 
       box-shadow: inset 0 0 3px 0px transparentize(darken($nav-bg, 60%), .4);
    }
  }
}
#nav-header{
  background-color: darken($header-bg, 3%);
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-start;
  align-items: stretch;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 5rem;
}
.nav-control{
  padding: 0;
  border: 0;
  margin: 0;
  width: 4rem;
  background-color: transparent;
  outline: none;
  cursor: pointer;
  position: relative;
  svg{
    $nav-icon-size: 1.5rem;
    width: $nav-icon-size;
    height: $nav-icon-size;
    
    transition: transform $transition-duration ease-in-out;
    path{
      fill: $primary;
      transition: fill $transition-duration ease-in-out;
    }
  }
  &:focus, &:active{
    svg{
      path{
        fill: adjust-color($color: $primary, $saturation: 1%, $lightness: 10%)
      }
    }
  }
  &:hover svg path {fill: adjust-color($color: $primary, $saturation: 1%, $lightness: 5%);}
}
#trigger-nav{
  grid-column: 1/span 1;
  grid-row: 1/span 1;
  justify-self: start;
  align-self: center;
  margin-left: 1rem;
  display: flex;
  svg{
    transform: scaleX(0.9);
  }
  &.on{
    grid-column: 2/span 1;
  }  
  #page-container.nav-pinned &{
    display: none;
  }
}
#pin-nav{
  svg{
    transition: transform $transition-duration ease-in-out;
    transform: rotateZ(45deg);
    path{
      transition: fill $transition-duration ease-in-out;
      fill: transparentize($orth-main, 0.5);
    }
  }
  &.on svg{
    transform: rotateZ(0deg);
    path{ fill: $primary-main; }
  }
}
#mask{
  opacity: 0;
  transform: scaleX(0);
  transition: opacity $transition-duration*2 ease-in-out;
  transition-delay: $transition-duration*2;
  background-color: transparentize($orth-light, 0.3);
  #main-nav.on~&, &.on{
    opacity: 1;
    transform: scaleX(1);
  }
  #main-nav.on~&{
    background-color: transparentize($orth-dark, 0.3);
  }
  #main-nav.pinned~&{
    transform: scaleX(0);
  }
  #main-nav.pinned~&.dialog.on, #main-nav.pinned~&.progress.on{
    transform: scaleX(1);
  }
}
#main-nav .content{
  .nav-list-item{
    .nav-link{
      text-decoration: none;
      color: $nav-fg;
      @include set-font-secondary();
      font-size: 1rem;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 0.15rem;
      padding: 0.9rem;
      display: block;
      &.selected{
        color: $nav-selected-fg;
        text-shadow: -1px -1px 1px black;
      }
    }
    .group-text{
      text-decoration: none;
      color: rgba(28, 29, 32, 0.62);
      font-family: 'Montserrat','San Francisco',Tahoma,sans-serif;
      font-size: 0.7rem;
      font-weight: 400;
      padding: 0.6rem .9rem 0.6rem;
      display: block;
      text-transform: uppercase;
      letter-spacing: -0.05rem;
      &.selected{
        color: $nav-selected-group-bg;
      }
    }

    .nav-link.selected{
      background-color: $nav-selected-bg;
      background-image: linear-gradient(to bottom, rgba(8, 43, 74, 0.12) 0%, rgba(5, 46, 84, 0.55) 100%)
    }
    .nav-group{
      // background-color: transparentize(black, 0.93);
      border: 1px solid transparentize(black, 0.9);
      border-width: 0 0 5px 0;
      &>.nav-link.selected{
        background-color: $nav-selected-group-bg;
      }
      .nav-group{
        background-color: transparent;
        border:0;
      }
      ul ul.nav-group .group-text{
        padding-left: 3rem;
      }
    }
  
    .nav-link:hover{
      color: $nav-hover-fg;
      background-color: $nav-hover-bg;
    }
    a:active, a:focus{
      outline-color: $nav-outline-color;
    }
    &.hidden{display: none;}
    &.disabled{
      pointer-events: none;
      .nav-link{
        color: lighten($orth-main, 10%);
      }
    }
  }
}